<template>
  <div class="p-2">
    <div id="header" class="mb-2">
      <h1 class="text-center display-2">Buscar usuario de GitHub</h1>
      <div class="input-group rounded">
        <input type="search" class="form-control rounded" placeholder="Introduce usuario de GitHub" aria-label="Search" aria-describedby="search-addon" v-model="usuario" @keydown.enter="obtenerUsuario" :disabled="desactivado" />
      </div>
      <div class="notexist rounded p-3 mt-3 border border-danger" :class="{oculto: userEncontrado}">
        Usuario no encontrado
      </div>
    </div>
    <main class="container-fluid row justify-content-center" :class="{oculto: !mostrarUser}">
        <div id="avatar-card" class="col-3 border rounded text-center p-2 container mx-2">
          <div class="mb-2">
            <img :src="imagen" alt="" class="avatar mx-auto">
          </div>
          <div class="text-center display-5 mb-3">
            {{usuarioLabel}}
          </div>
          <a class="btn btn-primary" href="#" @click="obtenerRepos">Repositorios</a>
          <a class="btn btn-light text-primary" :href="pagina" target="blank">PÃ¡gina GitHub</a>
        </div>
        <div id="repositorios" class="col-6 border rounded p-0 mx-0" :class="{oculto: !mostrarRepos}">
        <GitHubRepos v-for="r in repositorios" :key="r.id" :repo="r" class="p-0 "></GitHubRepos>
        </div>
        
    </main>
  </div>
</template>

<script>
import axios from 'axios';
import GitHubRepos from './GitHubRepos.vue'

export default {
  name: 'GitHub',

  data() {
    return {
      usuario: "",
      usuarioLabel: "",
      datos: {},
      desactivado: false,
      userEncontrado: true,
      mostrarUser: false,
      mostrarRepos: false,
      pagina: "",
      urlRepos : "",
      repositorios: []
    }
  },
  methods: {
    obtenerUsuario : function() {
      this.desactivado = true;
      console.log(typeof repos)
      var url = "https://api.github.com/users/" + this.usuario;
      console.log(url)
      this.pagina = "https://github.com/" + this.usuario;
      axios
        .get(url)
        .then(r => {
          var datos = r.data;
          this.datos = datos;
          this.imagen = this.datos.avatar_url;
          this.usuarioLabel = this.usuario;
          console.log(datos);
          this.userEncontrado = true;
          this.mostrarUser = true;
          this.urlRepos = this.datos.repos_url;
          this.pagina = this.datos.html_url;
          this.mostrarRepos = false;
        })
        .catch(error => {
          console.log(error.message)
          this.userEncontrado = false;
          this.mostrarUser = false;
        })
        .finally(() => {
          this.desactivado = false;
          console.log(typeof this.repositorios)
        })
  
    },
    obtenerRepos: function() {
      axios
        .get(this.urlRepos)
        .then(r => {
          var repos = r.data;
          this.repositorios = repos;
        })
      this.mostrarRepos = true;
    }
  },
  components: {
    GitHubRepos,
  }

}
</script>

